

//
// Buttons
// --------------------------------------------------

$btn-font-weight:                normal;


.xBtn {
  margin-bottom: 0; // For input.xBtn
  font-weight: $btn-font-weight;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
  border: 1px solid transparent;
  white-space: nowrap;
  transition: all .2s;
  @include button-size($padding-base-vertical, $padding-base-horizontal, $font-size-base, $line-height-base, $btn-border-radius-base);
  @include user-select(none);
  @include box-shadow(0 1px 1px $btn-boxshadow-color);
  color: $btn-color;
	display: inline-flex;
	align-items: center;
	justify-content: center;

	i{
		margin: 0 2px;
	}
  &,
  &:active,
  &.active {
    &:focus,
    &.focus {
	  outline: 0;
    }
  }

  &:hover,
  &:focus,
  &.focus {
    color: $btn-default-color;
    text-decoration: none;
  }

  &:active,
  &.active {
    outline: 0;
    background-image: none;
	@include box-shadow(inset 0 1px 2px $btn-active-boxshadow-color);
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: $cursor-disabled;
	@include opacity(.65);
	@include box-shadow(none);
  }

  // 按钮组中的隐藏按钮，添加 .displaynone，防止两端元素样式出现问题
  &.displaynone{
    display: none;
  }
}

a.xBtn {
  .xBtn.disabled,
  fieldset[disabled] .xBtn {
	pointer-events: none; // Future-proof disabling of clicks on `<a>` elements
  }
}

// Apply the mixin to the buttons
.xBtn-default { @include btn-styles($btn-default-bg,$btn-default-colorend,$btn-default-border,$btn-default-hover-bg,$btn-default-active-bg,$btn-default-active-bgend); }
.xBtn-primary { @include btn-styles($btn-primary-bg,$btn-primary-bg,$btn-primary-border,$btn-primary-hover-bg,$btn-primary-active-bg,$btn-primary-active-bg);}
.xBtn-success { @include btn-styles($btn-success-bg,$btn-success-bg,$btn-success-border,$btn-success-hover-bg,$btn-success-active-bg,$btn-success-active-bg);}
.xBtn-warning { @include btn-styles($btn-warning-bg,$btn-warning-bg,$btn-warning-border,$btn-warning-hover-bg,$btn-warning-active-bg,$btn-warning-active-bg);}
.xBtn-danger  { @include btn-styles($btn-danger-bg,$btn-danger-bg,$btn-danger-border,$btn-danger-hover-bg,$btn-danger-active-bg,$btn-danger-active-bg);}



.xBtn-default{
  color: $btn-color;
  &:hover,
  &:focus  {
	color: $btn-color;
  }

}




// Link buttons
// -------------------------

// Make a button look and behave like a link
.xBtn-link {
  color: $link-color;
  font-weight: normal;
  border-radius: 0;

  &,
  &:active,
  &.active,
  &[disabled],
  fieldset[disabled] & {
    background-color: transparent;
	@include box-shadow(none);
  }
  &,
  &:hover,
  &:focus,
  &:active {
    border-color: transparent;
  }
  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
    background-color: transparent;
  }
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus {
      color: $btn-link-disabled-color;
      text-decoration: none;
    }
  }
}


// Button Sizes
// --------------------------------------------------

.xBtn-lg {
  // line-height: ensure even-numbered height of button next to large input
  @include button-size($padding-large-vertical, $padding-large-horizontal, $font-size-base, $line-height-base, $btn-border-radius-base);
  &.xBtn-horpa-lg {
	padding-left: $padding-large-horizontal*2;
	padding-right: $padding-large-horizontal*2;
  }
  &.xBtn-horpa-xlg {
	padding-left: $padding-large-horizontal*4;
	padding-right: $padding-large-horizontal*4;
  }
}
.xBtn-sm {
  // line-height: ensure proper height of button next to small input
  @include button-size($padding-small-vertical, $padding-small-horizontal, $font-size-small, $line-height-small, $btn-border-radius-base);
  font-weight: normal;
  &.xBtn-horpa-lg {
	padding-left: $padding-small-horizontal*2;
	padding-right: $padding-small-horizontal*2;
  }
  &.xBtn-horpa-xlg {
	padding-left: $padding-small-horizontal*4;
	padding-right: $padding-small-horizontal*4;
  }
}


// 双倍横向边距padding，4倍横向边距padding
// --------------------------------------------------
.xBtn-horpa-lg {
  padding-left: $padding-base-horizontal*2;
  padding-right: $padding-base-horizontal*2;
}
.xBtn-horpa-xlg {
  padding-left: $padding-base-horizontal*4;
  padding-right: $padding-base-horizontal*4;
}




// Block button// --------------------------------------------------

.xBtn-block {
  display: block;
  width: 100%;
}

// Vertically space out multiple block buttons
.xBtn-block + .xBtn-block {
  margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.xBtn-block {
    width: 100%;
  }
}





// 多种类型按钮扩展
// --------------------------------------------------
.xBtn{
  transition: all .2s;
  .loading-icon{
	display: none;
  }
  &.loading{
	.loading-icon{
	  width: 20px;
	  height: 20px;
	  display: inline-block;
	  vertical-align: top;
	  stroke: $btn-loading-stroke;
	  fill: $btn-loading-fill;
	  -webkit-animation: xBtnLoadingIconAni .2s;
	  animation: xBtnLoadingIconAni .2s;
	}
  }
  // Apply the mixin to the buttons
  &.xBtn-default {
	&.loading{
	  .loading-icon{
		stroke: $btn-loading-default-stroke;
		fill: $btn-loading-default-fill;
	  }
	}
  }
  &.xBtn-link{
	&.loading{
	  .loading-icon{
		stroke: $btn-loading-link-stroke;
		fill: $btn-loading-link-fill;
	  }
	}
  }

}

@keyframes xBtnLoadingIconAni {
  from {
	width: 0;
  }
  to{
	width: 20px;
  }
}









